*{
    box-sizing: border-box;
}
.headerButtons{
    position: fixed; 
    width: 45%;
    z-index: 9999;      
    display: flex;
    justify-content: space-around;
}
.mainHeader{
    height: 170px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(78, 78, 78);
}
.containerButtons{
    width: 60%;
    height: 120px;
    position: fixed;
    z-index: 2000;
    border-radius: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}
.backgroundBlur {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.736);
    backdrop-filter: blur(2px);
    -webkit-backdrop-filter: blur(10px); 
    z-index: -1;
}
.catalogButton{
    width: 18%;
    height: 60px;
    background-color: rgb(78, 78, 78);
    color: white;
    border-color: transparent;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    cursor: pointer;
    font-size: 20px;
    transition: opacity 0.3s ease, color 0.3s ease, border-color 0.3s ease;
}
